
import React from "react";
import {getAllDependentsWithMedication} from "../../../api/dependents/Dependents";
import {getAllData} from "../../../api/data/Data"

import {formatDependentsWithMeds} from "../../../components/admin/AdminTable/medicationFormatter.js";
import AdminTable from "../../../components/admin/AdminTable/AdminTable";
import {Container,Alert} from 'reactstrap'
import Overview from "../../../components/Overview/Overview";
import {getPopUpForDependent,getPopUpForRxs,getPopUpForListRxsMedications} from '../../../components/admin/AdminTable/medicationFormatter';

class ViewMedication extends React.Component {

  state = {
    formattedData:null,
    error:{
      display:false,
      msg:null
    },
    overViewData:null
  }
  getDepsWithMeds = async(error) =>{
    if(error){
      let newState = this.state;
      newState.error.display = true;
      newState.error.msg = error;
      this.setState(newState);
      this.setTimerForError();
    }else{
      let newState = this.state;
      newState.formattedData = null;
      this.setState(newState);
      var jwt = localStorage.getItem("JWT");
      let res = await getAllDependentsWithMedication(jwt);
      //this.getDataForOverview();
      if(res.error){
        await this.getDepsWithMeds(res.error);
      }else{
          newState.formattedData = formatDependentsWithMeds(res);
          this.setState(newState);
          return true;
      }
    }
  }
  setTimerForError = () =>{
    let newState = this.state;
    newState.error.display = false;
    setTimeout(() => {
      this.setState(newState);
    }, 7000)
  }
  getDataForOverview=async()=>{
    /*let newState = this.state;
    var data = [];
    var jwt = localStorage.getItem("JWT");
    let res = await getAllData(jwt);
    data.push({
      title:"Number of Dependents:",
      data:res.total.dependent
    });
    newState.overViewData = data;
    this.setState(newState);*/
  }
  componentDidMount(){
    this.getDepsWithMeds();
    
  }

  render(){
    return(
      <>
        {this.state.error.display?<Alert color="danger">{this.state.error.msg}</Alert>:<Alert className="d-none" color="danger">{this.state.error.msg}</Alert>}
        <Container>
          {this.state.overViewData?
            <>
            {/* <Overview data={this.state.overViewData} iconColor={["red","yellow","blue"]} iconName={["users"]}/> */}
            </>
          :
          null}
          {this.state.formattedData? <AdminTable toggleInnerAdd={getPopUpForRxs} getAddPopUpData={getPopUpForDependent} hasInnerTable={true} updateData={this.getDepsWithMeds} data={this.state.formattedData}/>:null}
        </Container>
      </>
    );
  }
}
  

export default ViewMedication;
