import React from 'react';
import InnerHeader from './InnerHeader/InnerHeader';
import Column from '../Column/Column';
import myStyles from './InnerTableStyles';
import {Button} from 'reactstrap';
import uuid from "uuid";
class InnerTable extends React.Component{
    state={
        isHovered:[]
    }
    constructor(props){
        super(props);
    }
    loadIsHover = (rowNum) =>{
        let newState = this.state;
        for(var i=0;i<rowNum;++i){
            newState.isHovered.push(false);
        }
        return newState.isHovered;
    }
    handleHover(key,value){
        let newState = this.state;
        newState.isHovered[key] = value;
        for(var i=0;i<newState.isHovered.length;++i){
            if(i == key && value){
                newState.isHovered[i] = true;
            }else{
                newState.isHovered[i] = false;
            }
        }
        this.setState(newState);

    }
    render(){
        const items = () =>{
            return this.props.data.map((row,key)=>{
                this.state.isHovered.push(false);
                return (
                    <React.Fragment key={uuid.v4()}>
                
                            <tr className="innerTable" onMouseEnter={this.handleHover.bind(this,key,true)}
                             onMouseLeave={this.handleHover.bind(this,key,false)}>
                            {row.isHeader?
                            <InnerHeader className="innerTable" styles={myStyles.innerTable} data={row.cols}/>
                            :
                            <Column popUpCustomReq={this.props.popUpCustomReq} isUser={this.props.isUser} 
                            listPopUp={this.props.listPopUp} isInnerTable={true} innerPopUp={this.props.innerPopUp} 
                            addHandler={this.props.addHandler} editObj={row} showEdit ={this.state.isHovered[key]}
                             updateData={this.props.updateData} styles={myStyles.innerTable} data={row.cols}/>
                            }
                            </tr>
                            {this.props.data.length-1 == key && this.props.isUser!=true?
                                <tr style={{backgroundColor:'white'}}>
                                    <td className="innerTable" colSpan={this.props.data[0].cols.length} 
                                    style={{textAlign:"center"}}>
                                        <Button onClick={this.props.innerPopUp.bind(this,this.props.parentId)} 
                                        color="success">Add Data</Button>
                                    </td>
                                </tr>
                            :null
                            }
                        
                    </React.Fragment>
                )
            });
        }
       return(
            <>
                {items()}
            </>
       )
    }
}
export default InnerTable;