import React from "react";
import {Row,Col} from "reactstrap"
import OverviewCard from './card/OverviewCard';

class Overview extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <>
                <Row style={{marginBottom:"30px"}}>
                    <Col size={4} style={{marginLeft:"0px",paddingLeft:"0px"}}>
                        <OverviewCard title={this.props.data[0].title} data={this.props.data[0].data} iconName={this.props.iconName[0]} iconColor={this.props.iconColor[0]}/>
                    </Col>
                    <Col size={4}>1</Col>
                    <Col size={4}>1</Col>
                </Row>
            </>
        )
    }
}

export default Overview;