import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { Button, Input} from 'reactstrap';

class ColInputList extends React.Component{
    state={
        inputValues:[],
    }
    constructor(props){
        super(props);
    }
    deleteHandler = (e,id) =>{
        this.props.data.deleteReq(this.props.data.depId,id,(res)=>{
            if(res.error){
                this.props.updateData(res.error);
            }else{
                this.props.updateData();
            }
        });
    }
    render(){
        var tag = {
            backgroundColor:"#4e73df",
            color:"white",
            marginLeft:"5px",
            padding:"4px"
        }
        const items = () =>{
            return this.props.data.items.map((item,itemKey)=>{
                return(
                    <>
                        {
                            item.map((ele,eleKey)=>{
                                return(
                                    <span style={tag}>{ele.value}<FontAwesomeIcon 
                                    onClick={(e)=>{this.deleteHandler(e,this.props.data.id[itemKey])}} size={'1x'}
                                     style={{marginLeft:'15px',cursor:'pointer'}} icon={faTimes} /></span>
                                );
                            })
                        }
                    </>
                );
            });
        }
        var buttonStyles = {
            height:'5px',
            fontSize:'13px',
            paddingTop:'0px',
            paddingBottom:'20px',
            borderRadius:'3px',
            fontWeight:'300',
            marginLeft:'5px',
            backgroundColor:'#2DCE89',
            color:'white',
            border:'none',
            marginBottom:'3px'
        }
        var undoBtn = {
            height:'5px',
            fontSize:'13px',
            paddingTop:'0px',
            paddingBottom:'20px',
            borderRadius:'3px',
            fontWeight:'300',
            marginLeft:'1px',
            backgroundColor:'#F5365C',
            color:'white',
            border:'none',
            marginBottom:'3px'
        }
        return(
                <>
                     {items()}
                     {/* <Button style={buttonStyles} onClick={this.props.innerPopUp.bind(this,this.props.parentId)}>Add
                     </Button> */}
                     <Button style={buttonStyles} 
                     onClick={this.props.addHandler.bind(this,this.props.data.listItems,true,null,false)}>Add</Button>
                     <Button style={undoBtn} onClick={this.props.undoHandler.bind(this,this.props.colIndex)}>Undo</Button>
                </>
        )
    }
}

export default ColInputList;