import React from 'react';
import { Col, Row, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faCheck,faUndo} from '@fortawesome/free-solid-svg-icons';



class ColInput extends React.Component{
    state={
        inputValues:[],
        isPush:false
    }
    constructor(props){
        super(props);
    }
    onChangeHandler = (e) =>{
        var newState = this.state;
        newState.isPush = true;
        var value = e.target.value;
        newState.inputValues[e.target.id].value = value;
        newState.inputValues[e.target.id].isChanged = true;
        this.setState(newState);
    }
    getChangedInputs = () =>{
        var changedValues = [];
        for(var i=0;i<this.state.inputValues.length;++i){
            if(this.state.inputValues[i].isChanged){
                changedValues.push(this.state.inputValues[i]);
            }
        }
        return changedValues;
    }
    render(){
        var getOptions = (item) =>{
            return item.options.map((option,optionKey)=>{
                return (
                    <>
                        <option selected value={option}>{option}</option>
                    </>
                
                )
            })
        }
        const items = () =>{
            return this.props.data.items.map((item,itemKey)=>{
                if(!this.state.isPush){
                    this.state.inputValues.push({
                        value:item.value,
                        isChanged:false,
                        index:itemKey,
                        path:item.path
                    });
                }
                var size = 3;
                if(this.props.data.type=="date" || this.props.data.type=="select"){
                    size = 5
                }else if(typeof(this.props.data.size)!='undefined'){
                    size = this.props.data.size;
                }
                return(
                    <Col md={size}>
                        <FormGroup style={{marginBottom:'0px'}}>
                        <style dangerouslySetInnerHTML={{__html: `
                        input[type=date]::-webkit-inner-spin-button {
                            -webkit-appearance: none;
                            display: none;
                        }
                        `}} />
                            {this.props.data.type == "select"?
                            <Input style={{height:'40px',marginTop:'-5px'}} type={this.props.data.type} onChange={this.onChangeHandler} id={itemKey} placeholder={item.placeHolder} value={this.state.inputValues[itemKey].value}>
                                {getOptions(this.props.data)}
                            </Input>
                            :
                            <Input style={{height:'5px'}} style={{width:'100%',height:'5px'}} type={this.props.data.type} onChange={this.onChangeHandler} id={itemKey} placeholder={item.placeHolder} value={this.state.inputValues[itemKey].value}/>
                            }
                        </FormGroup>
                    </Col>
                );
            });
        }
        var iconStyles={
            cursor:"pointer",
        }
        return(
                <Row form>
                    {items()}
                    <Col med={3}>
                        <FontAwesomeIcon style={{cursor:'pointer'}} size={'2x'} color="#2DCE89" icon={faCheck} onClick={this.props.sumbitHandler.bind(this,this.props.colIndex,this.getChangedInputs())}/>
                        <FontAwesomeIcon style={{cursor:'pointer'}} size={'2x'} color="#E02D1B" style={{marginLeft:"15px"}} icon={faUndo} onClick={this.props.undoHandler.bind(this,this.props.colIndex)}/>
                    </Col>
                </Row>
        )
    }
}

export default ColInput;