const validator = require('../../../config/validator');
const userPatch = require('../../../api/users/Users').patch;
const CreateSendEmail = require('../../../api/email/email').sendWelcome;
const userDelete = require('../../../api/users/Users').userDelete;

function formatUsers(users){
    var obj = null
    var rows = [];
    //Get Header
    rows.push(getHeaderForUsers());
    for(var i=0;i<users.length;++i){
        rows.push(getRowUsers(users[i]));
    }
    obj = {
        rows:rows
    }
    return obj;
}
function getHeaderForUsers(){
    var cols = [];
    cols.push("Email:");
    cols.push("Is Admin:");
    cols.push("Status:");
    cols.push("Options:")
    var row = {
        isInnerTable:false,
        isHeader:true,
        cols:cols
    }
    return row;
}

function getColsForUsers(user){
    var cols = [];
    cols.push({
        isEditable:false,
        containsDropDown:true,
        type:"text",
        id:user._id.toString(),
        patchReq:userPatch,
        items:getItems([user.username],["Email:"],null,["username"])
    });
    cols.push({
        isEditable:true,
        containsDropDown:true,
        type:"select",
        options:["false","true"],
        id:user._id.toString(),
        patchReq:userPatch,
        items:getItems([user.isAdmin.toString()],["Is Admin:"],null,["isAdmin"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:true,
        type:"text",
        id:user._id.toString(),
        patchReq:userPatch,
        items:getItems([user.auth.status.statusValue],["Status:"],null,["statusValue"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:user._id.toString(),
        patchReq:userPatch,
        items:getItems([null],[null],
            [{name:"user:"+user.username,
                id:user._id.toString(),
                remove:userDelete
            }]
            ,null)
    });
    return cols;
}

function getRowUsers(user){
    var row = {
        isInnerTable:false,
        isHeader:false,
        cols:getColsForUsers(user)
    }
    return row;
}
function getItems(valueList,placeHolderList,isObject,path){
    var items = [];
    for(var i=0;i<valueList.length;++i){
        items.push({
            value:valueList[i],
            placeHolder:placeHolderList[i],
            isObject:isObject,
            path:path
        });
    }
    return items;
}
function getPopUpForUser(selfId,parentId,email,isAdmin){
    var data = [];

    data.push({
        value:email,
        type:"email",
        validator:validator.emailValidator,
        label:"Email of User:",
        placeHolder:"Enter email of user.",
        required:true,
        path:"username",
        reqCreate:CreateSendEmail,
        selfId:selfId,
        parentId:parentId
    });
   var indexSelected = 0;
   var options = [false,true];
   indexSelected = 1;
   for(var i=0;i<options.length;++i){
       if(options[i]==isAdmin){
           indexSelected = i;
       }
   }
    data.push({
        value:isAdmin,
        type:"select",
        validator:null,
        options:options,
        label:"Select if User is Admin:",
        placeHolder:null,
        required:false,
        path:"isAdmin",
        indexSelected:indexSelected
    });

    return data;
}
module.exports={formatUsers,getPopUpForUser}