import React from 'react';
import uuid from "uuid";
class Header extends React.Component{
    constructor(props){
        super(props);

    }
    render(){
        const items = () =>{
            return this.props.headerItems.map((item,key)=>{
                return(
                    
                        <React.Fragment key={uuid.v4()}> 
                        {key==3?
                        <th style={{borderTop:"none"}} rowSpan="2" key={"HeaderItem"+key}>{item}</th>
                        :<th style={{borderTop:"none"}} key={"HeaderItem"+key}>{item}</th>
                        }
                        </React.Fragment>
                );
            });
        }
        return(
            <thead style={{border:"none",borderCollapse:"collapse"}}>
                <tr>
                    {items()}
                </tr>
            </thead>
        )
    }
}
export default Header;
