import React from 'react';
import { Dropdown, DropdownMenu, DropdownToggle } from 'reactstrap';


class List extends React.Component{
    state={
        isOpen:[]
    }
    constructor(props){
        super(props);
    }
    toggle=(key)=>{
        let newState = this.state;
        newState.isOpen[key] = !newState.isOpen[key];
        this.setState(newState);
    }

    render(){

        const dropDownStyle={
            fontSize: "0.8125rem",
            backgroundColor:"white"
        }
        const list = ()=>{
            return this.props.data.items.map((item,key)=>{
                return(
                    <>
                        {
                            item.map((ele,eleKey,array)=>{
                                var textContent = ele.value;
                                var myStyle = {
                                    paddingLeft:'0px',
                                    color:"#233DDA",
                                    cursor:"pointer"
                                };
                                if(this.props.data.items.length>1 && key != this.props.data.items.length-1){
                                    textContent = textContent + ",";
                                    myStyle = {
                                        paddingLeft:"5px",
                                        color:"#233DDA",
                                        cursor:"pointer"
                                    }
                                }
                                if(key==0){
                                    myStyle = {
                                        paddingLeft:"0px",
                                        color:"#233DDA",
                                        cursor:"pointer"
                                    }
                                }else{
                                    myStyle = {
                                        paddingLeft:"5px",
                                        color:"#233DDA",
                                        cursor:"pointer"
                                    }
                                }
                                this.state.isOpen.push(false);
                                return(
                                    <>
                                        {this.props.isUser?
                                        <>
                                            {/* <a onClick={()=>{alert("ISUSER")}} style={myStyle}>{textContent}</a> */}

                                                <a  onClick={this.toggle.bind(this,key)} style={myStyle}>{textContent}</a>
                                        </>
                                        :
                                        <a onClick={this.props.listPopUp.bind(this,this.props.data.listItems,false,key)} style={myStyle}>{textContent}</a>
                                        }
                                    </>
                                )
                            })
                        }
                    </>
                );
            });
        }
        return(
            <>
                {this.props.data.id.length>0?
                    <>
                    {list()}
                    </>
                    :this.props.isEdit?
                    "":
                    "No "+this.props.data.name
                }
            </>
        );
    }
}

export default List;