const dependentPatch = require('../../../api/dependents/Dependents').dependentPatch;
const rxsPatch = require('../../../api/medication/Rxs').rxsPatch;
const medicationPatch = require('../../../api/medication/Medication').medicationPatch;
const dependentDelete = require('../../../api/dependents/Dependents').deleteDep;
const rxsDelete = require('../../../api/medication/Rxs').deleteRxs;
const removeOTCFromDep = require('../../../api/dependents/Dependents').removeOTCFromDep;
const removeRxsMedFromDep = require('../../../api/medication/Rxs').removeMedFromArr;
const createAndAddRxsMedicationToRxs = require('../../../api/medication/Rxs').createAndAddRxsMedicationToRxs;
const createAndAddOTCMedicationToDependent = require('../../../api/dependents/Dependents').createAndAddOTCMedicationToDependent;
const rxsMedicationPatch = require('../../../api/medication/rxsMedication').rxsMedicationPatch;
const createDependent = require('../../../api/dependents/Dependents').create;
const createRxsAndToDependent = require('../../../api/medication/Rxs').createAndAddToDependent;
const validator = require('../../../config/validator');

var csrfToken = null;

function formatDependentsWithMeds(dependents){
    var obj = null
    var rows = [];
    //Get Header
    if(typeof(dependents)=='undefined'){
        return null;
    }
    rows.push(getHeaderForDependents());
    for(var i=0;i<dependents.length;++i){
        rows.push(getRowDependentsWithMeds(dependents[i]));
        rows.push(getHeaderForRxs());
        for(var ix=0;ix<dependents[i].rxs.length;++ix){
            if(ix==0){
                //rows.push(getHeaderForRxs());
            }
            var rxs = dependents[i].rxs[ix];
            var medications = [];
            var dependentId = dependents[i]._id.toString();
            for(var ixx=0;ixx<dependents[i].rxs[ix].rxsMedications.length;++ixx){
                medications.push(dependents[i].rxs[ix].rxsMedications[ixx]);
            }
            rows.push(getRowWithMeds(rxs,medications,dependentId));
        }
    }
    obj = {
        rows:rows
    }
    return obj;
}
function getHeaderForDependents(){
    var cols = [];
    cols.push("Name:");
    cols.push("Date of Birth:");
    cols.push("OTC Medications:");
    cols.push("")
    cols.push("Options:")
    var row = {
        isInnerTable:false,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getHeaderForRxs(){
    var cols = [];
    cols.push("Rxs Number:");
    cols.push("Medications:");
    cols.push("Doctor's Name:");
    cols.push("Doctor's #:");
    cols.push("Options:")
    var row = {
        isInnerTable:true,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getColsForDependents(dependent){
    var cols = [];
    cols.push({
        isEditable:true,
        containsDropDown:true,
        type:"text",
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:dependentPatch,
        items:getItems([dependent.name.firstName,dependent.name.lastName],["First Name:","Last Name:"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:true,
        type:"date",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:dependentPatch,
        items:getItems([dependent.dateOfBirth],["Date of Birth:"],null,["dateOfBirth"])
    });
    var medIds = [];
    var medItems = [];
    var listItems = [];
    if(dependent.medications.length>0){
        for(var i=0;i<dependent.medications.length;++i){
            medIds.push(dependent.medications[i]._id.toString());
            medItems.push(getItems([dependent.medications[i].name],["Medication Name"],null,["name"]));
            listItems.push(getPopUpForListOTCMedications(dependent.medications[i]._id,dependent._id,dependent.medications[i].name));
        }
    }else{
        listItems.push(getPopUpForListOTCMedications(null,dependent._id,null))
    }
    cols.push({
        isEditable:true,
        type:"text",
        isList:true,
        listItems:listItems,
        containsDropDown:false,
        id:medIds,
        name:"Medications",
        parentId:dependent._id.toString(),
        depId:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:dependentPatch,
        deleteReq:removeOTCFromDep,
        items:medItems
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        patchReq:dependentPatch,
        csrfToken:csrfToken,
        items:getItems([""],["Date of Birth:"],null,["dateOfBirth"])
    });
    /*end of dummy col*/
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:dependentPatch,
        items:getItems([null],[null],
            [{name:"dependent:"+dependent.name.firstName+" "+dependent.name.lastName,
                id:dependent._id.toString(),
                remove:dependentDelete
            }]
            ,null)
    });
    return cols;
}
//
function getColsForMedication(rxs,medications,dependentId){
    var cols = [];
    var medItems = [];
    var medIds = [];
    var listItems = [];

    cols.push({
        isEditable:true,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:rxsPatch,
        csrfToken:csrfToken,
        items:getItems([rxs.rxsNumber],["Rxs Number:"],null,["rxsNumber"])
    });

    if(medications.length>0){
        for(var i=0;i<medications.length;++i){
            medIds.push(medications[i]._id.toString());
            medItems.push(getItems([medications[i].name],["Medication Name"],null,["name"]));
            listItems.push(getPopUpForListRxsMedications(medications[i]._id,rxs._id,medications[i].name,medications[i].dosage,medications[i].reason,medications[i].datePrescribed,medications[i].instructions,medications[i].endDate,medications[i].whenToTake.value));
        }
    }else{
        listItems.push(getPopUpForListRxsMedications(null,rxs._id,null,null,null,null,null,null,null));
    }
    cols.push({
        isEditable:true,
        type:"text",
        isList:true,
        parentId:rxs._id.toString(),
        depId:rxs._id.toString(),
        listItems:listItems,
        containsDropDown:false,
        id:medIds,
        csrfToken:csrfToken,
        name:"Medications",
        deleteReq:removeRxsMedFromDep,
        patchReq:rxsMedicationPatch,
        items:medItems
    });


    cols.push({
        isEditable:true,
        type:"text",
        isList:false,
        containsDropDown:false,
        id:rxs._id.toString(),
        patchReq:rxsPatch,
        csrfToken:csrfToken,
        items:getItems([rxs.doctorContacts.name.firstName,rxs.doctorContacts.name.lastName],["Doctor's First Name","Doctor's Last Name"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:true,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id.toString(),
        patchReq:rxsPatch,
        csrfToken:csrfToken,
        items:getItems([rxs.doctorContacts.phoneNumber],["Doctor's #"],null,["phoneNumber"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        isList:false,
        containsDropDown:false,
        id:rxs._id.toString(),
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([null],[null],
            [{name:"prescribtion#"+rxs.rxsNumber,
                id:rxs._id.toString(),
                remove:rxsDelete
            }],null)
    });
    return cols;
}
function getRowWithMeds(rxs,medication,dependentId){
    var row = {
        isInnerTable:true,
        isHeader:false,
        cols:getColsForMedication(rxs,medication,dependentId)
    }
    return row;
}
function getRowDependentsWithMeds(dependent){
    var row = {
        isInnerTable:false,
        isHeader:false,
        cols:getColsForDependents(dependent)
    }
    return row;
}
function getNumOfMeds(rxss){
    var num = 0;
    for(var i=0;i<rxss.length;++i){
        num = num + rxss[i].medications.length;
    }
    return num;
}
function getItems(valueList,placeHolderList,isObject,path){
    var items = [];
    for(var i=0;i<valueList.length;++i){
        items.push({
            value:valueList[i],
            placeHolder:placeHolderList[i],
            isObject:isObject,
            path:path
        });
    }
    return items;
}
function getPopUpForListRxsMedications(selfId,parentId,name,dosage,reason,datePrescribed,instructions,endDate,whenToTakeValue){
    var data = [];
    //REMEMBER TO ADD PATCH TO THE FIRST ELEMENT!!!!
    data.push({
        value:name,
        type:"string",
        validator:validator.nameValidator,
        label:"Name of Medication",
        placeHolder:"Enter name of medication",
        required:true,
        path:"name",
        reqCreate:createAndAddRxsMedicationToRxs,
        reqUpdate:rxsMedicationPatch,
        selfId:selfId,
        parentId:parentId
    });

    data.push({
        value:dosage,
        type:"number",
        validator:validator.numberValidator,
        label:"Dosage of Medication",
        placeHolder:"Enter dosage",
        required:true,
        path:"dosage"
    });

    data.push({
        value:reason,
        type:"string",
        validator:null,
        label:"Reason of Medication",
        placeHolder:"Enter reason",
        required:true,
        path:"reason"
    });

    data.push({
        value:datePrescribed,
        type:"date",
        validator:null,
        label:"Date Prescribed",
        placeHolder:"Enter date",
        required:true,
        path:"datePrescribed"
    });

    data.push({
        value:instructions,
        type:"string",
        validator:null,
        label:"Instructions of Medication",
        placeHolder:"Enter instructions",
        required:false,
        path:"instructions"
    });

    data.push({
        value:endDate,
        type:"date",
        validator:null,
        label:"End Date of Medication",
        placeHolder:"Enter date",
        required:false,
        path:"endDate"
    });

    var indexSelected = 0;
    var options = [" -- select an option --","morning","afternoon","evening"];
    for(var i=0;i<options.length;++i){
        if(options[i]==whenToTakeValue){
            indexSelected = i;
        }
    }
    data.push({
        value:whenToTakeValue,
        type:"select",
        validator:null,
        options:options,
        label:"When To Take Medication",
        placeHolder:null,
        required:false,
        path:"value",
        indexSelected:indexSelected
    });

    return data;
}
