
import React from "react";
import {getAllUsers} from "../../../api/users/Users";

import {formatUsers,getPopUpForUser} from "../../../components/admin/AdminTable/userFormatter.js";
import AdminTable from "../../../components/admin/AdminTable/AdminTable";
import {Container,Alert} from 'reactstrap'

class ViewUsers extends React.Component {

  state = {
    formattedData:null,
    error:{
      display:false,
      msg:null
    }
  }
  getUsers = async(error) =>{
    if(error){
      let newState = this.state;
      newState.error.display = true;
      newState.error.msg = error;
      this.setState(newState);
      this.setTimerForError();
    }else{
      let res = await getAllUsers(localStorage.getItem("JWT"));
      let newState = this.state;
      if(res.error){
        await this.getUsers(res.error);
      }else{
          newState.formattedData = formatUsers(res);
          this.setState(newState);
          return true;
      }
    }
  }
  setTimerForError = () =>{
    let newState = this.state;
    newState.error.display = false;
    setTimeout(() => {
      this.setState(newState);
    }, 7000)
  }
  componentDidMount(){
    this.getUsers();
  }

  render(){
    return(
      <>
        {this.state.error.display?<Alert color="danger">{this.state.error.msg}</Alert>:<Alert className="d-none" color="danger">{this.state.error.msg}</Alert>}
        <Container>
          {this.state.formattedData? <AdminTable getAddPopUpData={getPopUpForUser} isInnerTable={false} updateData={this.getUsers} data={this.state.formattedData}/>:null}
        </Container>
      </>
    );
  }
}
  

export default ViewUsers;
