import React from 'react';

import ColInput from './ColInput/ColInput';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPen,faAngleDown,faAngleUp } from '@fortawesome/free-solid-svg-icons';
import Edit from './edit/Edit';
import List from './list/list';
import {Badge} from 'reactstrap';
import ColInputList from './ColInputList/ColInputList';
import uuid from "uuid";


class Column extends React.Component{
    state={
        inputShow:[],
        oldData:[],
        isUndo:false
    }
    constructor(props){
        super(props);
    }
    toggleInput = (key) =>{
        let newState = this.state;
        newState.inputShow[key] = !newState.inputShow[key];
        this.setState(newState);
    }
    toggleUndo = () => {
        let newState = this.state;
        newState.isUndo = !newState.isUndo;
        return newState;
    }
    handleSubmit=async(colKey,newData)=>{ 
        var id = this.props.data[colKey].id;
        var obj = {};
        for(var i=0;i<newData.length;++i){
            var item = newData[i];
            var key = item.path[item.index];
            if(item.value){
                obj[key] = item.value;
            }
        }
        this.props.data[colKey].patchReq(id,{updatedFields:obj,_csrf:this.props.data[colKey].csrfToken},(res)=>{
            if(res.error){
                var error = res.error;
                if(typeof(res.error)=='object'){
                    error = JSON.stringify(error);
                }
               this.props.updateData(error);
            }else{
               this.props.updateData();
            }
        });
    }
    render(){
        const cols = () =>{
            return  this.props.data.map((col,colKey)=>{
                if(this.state.isUndo){
                    col = this.state.oldData.data[colKey];
                }

                this.state.inputShow.push(false);


                var textValue = "";
                if(col.items.length>1){
                    for(var i=0;i<col.items.length;++i){
                        textValue = textValue+" "+col.items[i].value;
                    }
                }else if(col.isList){
                    textValue = "list"
                }else{
                    textValue = col.items[0].value
                }


                let toggleRow;
                var isFirstCol = false;
                if(col.containsDropDown && !this.props.innerTableValue && this.props.hasInnerTable){
                    isFirstCol = true;
                    toggleRow =  <FontAwesomeIcon 
                    onClick={this.props.toggleTable.bind(this,this.props.innerTableIndex,this.props.data[0].id)}
                     size={'2x'} style={{marginLeft:'10px',cursor:'pointer',verticalAlign: 'middle',marginRight:'10px'}}
                     icon={faAngleDown} />;
                }else if(col.containsDropDown && this.props.hasInnerTable){
                    isFirstCol = true;
                    toggleRow = <FontAwesomeIcon 
                    onClick={this.props.toggleTable.bind(this,this.props.innerTableIndex,this.props.editObj.dependent.id,
                        this.props.data[0].id)} size={'2x'} 
                        style={{marginLeft:'10px',cursor:'pointer',verticalAlign: 'middle',marginRight:'10px'}}
                        icon={faAngleUp} />;
                }else{
                    toggleRow = null;
                }

                let data;
                if(textValue=="pending"){
                    data = [
                    <span style={{fontWeight:'bold'}}>
                        <Badge color="" className="badge-dot mr-4" style={{fontWeight:'bold'}}>
                            <i className="bg-warning" style={{fontWeight:'bold'}}/>
                                {textValue}
                            </Badge>
                    </span>];
                }else if(textValue=="approved"){
                    data = [
                        <span key={uuid.v4()} style={{fontWeight:'bold'}}>
                            <Badge key={uuid.v4()} color="" className="badge-dot mr-4" style={{fontWeight:'bold'}}>
                                <i className="bg-success" style={{fontWeight:'bold'}}/>
                                    {textValue}
                                </Badge>
                        </span>];
                }else if(col.type == 'date'){
                    var year = textValue.substr(0,4);
                    var day = textValue.substr(5,2);
                    var month = textValue.substr(8,2);
                    textValue = day+"-"+month+"-"+year;
                    data = [<span key={uuid.v4()} style={{fontWeight:'bold'}}>{textValue}</span>];

                }else{
                    if(typeof(textValue)=='string'){
                        var textValueArr = textValue.split(" ");
                        var newArr = [];
                        var textValue = "";
                        for(var i=0;i<textValueArr.length;++i){
                            if(i%4==0 && i!=0){
                                //newArr.push("&#10;&#13;");
                                textValue = textValue + "\n";
                            }
                            textValue = textValue + textValueArr[i] + " ";
                        }
                    }
                    if(isFirstCol){
                        data = [<span key={uuid.v4()} style={{fontWeight:'bold'}}>{textValue}</span>];
                    }else{
                        data = [<span key={uuid.v4()} style={{fontWeight:'bold',whiteSpace: 'pre-wrap'}}>{textValue}</span>];
                    }
                   
                }
            
 

                let listData



                if(col.isEditable){
                    listData = <FontAwesomeIcon key={uuid.v4()} color={'#f4b619'} onClick={this.toggleInput.bind(this,colKey)}
                     style={{marginRight:'48%',float:"right",cursor:'pointer'}} icon={faPen} />;
                    data.push(<FontAwesomeIcon key={uuid.v4()} color={'#f4b619'} onClick={this.toggleInput.bind(this,colKey)}
                     style={{marginRight:'48%',float:"right",cursor:'pointer'}} icon={faPen} />);
                }

                let showInput = {}
                if(this.state.inputShow[colKey] && col.items.length>0 && col.type !='select' &&  col.type !='date'){
                    showInput.display = 'inline-flex';
                }
                showInput.border = "none";

                var tableOptionClass = "innerTable"
                if(!this.props.isInnerTable){
                    tableOptionClass = "";
                }


                return(
                        <React.Fragment key={uuid.v4()}>
                            {   this.state.inputShow[colKey]&& col.isList?
                                <td style={this.props.styles}><ColInputList innerPopUp={this.props.innerPopUp} 
                                listPopUp={this.props.listPopUp} data={col} addHandler={this.props.addHandler}
                                 undoHandler={this.toggleInput} updateData={this.props.updateData} colIndex={colKey} 
                                 sumbitHandler={this.handleSubmit}/></td>
                                :this.state.inputShow[colKey]?
                                <td style={showInput}>
                                    {toggleRow}
                                    <ColInput style={this.props.styles} data={col} undoHandler={this.toggleInput}
                                     colIndex={colKey} sumbitHandler={this.handleSubmit}/>
                                </td>
                                :col.isList?
                                <td style={this.props.styles}><List isUser={this.props.isUser}
                                 listPopUp={this.props.listPopUp} innerPopUp={this.props.innerPopUp} 
                                 isEdit={false} data={col}/>{listData}</td>
                                :col.items[0].isObject && this.props.showEdit?
                                <td className={tableOptionClass} style={{borderTop:'none'}}>
                                    <Edit popUpCustomReq={this.props.popUpCustomReq} style={{borderTop:'none'}}
                                     updateData = {this.props.updateData}  innerPopUp={this.props.innerPopUp} 
                                     editObj={col.items[0].isObject}/>
                                </td>
                                :
                                <td style={this.props.styles}>
                                    {toggleRow}
                                    {data}
                                </td>
                            }

                        </React.Fragment>
                )
            })
    
        }
        return(
            <>
                {cols()}
            </>
        )
    }
}
export default Column;