import React from 'react';
import uuid from "uuid";
class InnerHeader extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        const items = () =>{
            return this.props.data.map((row,key)=>{
                return(
                    <td key={uuid.v4()} className="innerTable" style={{fontWeight:'bold',background:'#dddfeb'}}>{row}</td>
                )
            });
        }
       return(
           <>
            {items()}
           </>
       )
    }
}
export default InnerHeader;