import React from 'react';

import Header from '../Header/Header';
import Column from './Column/Column';
import InnerTable from './InnerTable/InnerTable';
import uuid from "uuid";


class Body extends React.Component{
    state = {
        data:null,
        tableShow:[],
        isHovered:[],
        parentId:null
    }
    constructor(props){
        super(props);
    }
    componentDidMount=()=>{
        let newState = this.state;
        newState.data = this.loadData();
        newState.isHovered = this.loadIsHover(newState.data.length);
        this.setState(newState);
    }
    handleHover(key,value){
        let newState = this.state;
        newState.isHovered[key] = value;
        for(var i=0;i<newState.isHovered.length;++i){
            if(i == key && value){
                newState.isHovered[i] = true;
            }else{
                newState.isHovered[i] = false;
            }
        }
        this.setState(newState);

    }
    loadIsHover = (rowNum) =>{
        let newState = this.state;
        for(var i=0;i<rowNum;++i){
            newState.isHovered.push(false);
        }
        return newState.isHovered;
    }
    loadData(){
        var rows = this.props.rows;
        var dependent = [];
        var lastIndex = 0;
        var table = [];
        for(var i=0;i<rows.length;++i){
            if(!rows[i].isInnerTable){
                dependent.push({
                    dependent:rows[i]
                });
                lastIndex = i;
                table = [];
            }else{
                table.push(rows[i]);
                if(typeof(dependent[lastIndex])=='undefined'){
                    dependent[dependent.length-1].table = table;
                }else{
                    dependent[lastIndex].table = table
                }
            }
        }
        return dependent;
    }
    toggleInnerTable=(myKey,id)=>{
        let newState = this.state;
        newState.parentId = id;
        newState.tableShow[myKey] = !newState.tableShow[myKey];
        this.setState(newState);
    }
    render(){
        const items = () =>{
            return this.state.data.map((row,rowKey)=>{
                return (
                    <React.Fragment key={uuid.v4()}>   
                        {rowKey!=0?
                        <>
                        <tr>
                            <td colSpan="100%" style={{borderTop:"solid",borderColor:"#EBEEF3",padding:"0px"}}></td>
                        </tr>
                        </>
                        :null
                        }
                        {

                        }
                        <tr key={"Row"+rowKey} onMouseEnter={this.handleHover.bind(this,rowKey,true)} 
                        onMouseLeave={this.handleHover.bind(this,rowKey,false)}>
                            <Column isUser={this.props.isUser} hasInnerTable={this.props.hasInnerTable}
                            listPopUp={this.props.listPopUp} isInnerTable={false} addHandler={this.props.addHandler}
                            innerPopUp={this.props.innerPopUp} editObj={row} showEdit = {this.state.isHovered[rowKey]}
                            updateData={this.props.updateData} toggleTable={this.toggleInnerTable}
                            data={row.dependent.cols} key={"rowCol"+rowKey} innerTableIndex={rowKey} 
                            styles={{border:'none'}} innerTableValue={this.state.tableShow[rowKey]}/>
                        </tr>
                        {this.state.tableShow[rowKey] && this.props.hasInnerTable?
                        <InnerTable key={uuid.v4()} popUpCustomReq={this.props.popUpCustomReq} isUser={this.props.isUser} 
                        innerPopUp={this.props.innerPopUp} parentId={this.state.parentId}
                        addHandler={this.props.addHandler} listPopUp={this.props.listPopUp} 
                        data={row.table} updateData={this.props.updateData} key={"innerTable"+rowKey}/>
                        :null}
                    </React.Fragment>
                );
            });
        }
        return(
            <>
            {this.state.data ? items():null}
            </>
        );
    }
}

export default Body;