
import React from "react";
import {getAllGroups} from '../../../api/groups/Groups';

import {formatGroups,getPopUpForGroup,getPopUpForGroupNew} from "../../../components/admin/AdminTable/groupFormatter.js";
import AdminTable from "../../../components/admin/AdminTable/AdminTable";
import {Container,Alert} from 'reactstrap'

class ViewGroup extends React.Component {

  state = {
    formattedData:null,
    error:{
      display:false,
      msg:null
    }
  }
  getGroups = async(error) =>{
    if(error){
      let newState = this.state;
      newState.error.display = true;
      newState.error.msg = error;
      this.setState(newState);
      this.setTimerForError();
    }else{
      let newState = this.state;
      newState.formattedData = null;
      this.setState(newState);

      let res = await getAllGroups(localStorage.getItem("JWT"));
      if(res.error){
        await this.getGroups(res.error);
      }else{
          newState.formattedData = formatGroups(res);
          this.setState(newState);
          return true;
      }
    }
  }

 getPopUpForGroup = async(id)=>{
   let res = null;
  await getPopUpForGroup(id).then(data=>{
     res = data;
  });

     return res;
 }
 

  setTimerForError = () =>{
    let newState = this.state;
    newState.error.display = false;
    setTimeout(() => {
      this.setState(newState);
    }, 7000)
  }
  componentDidMount(){
    this.getGroups();
    // this.getPopUpForGroup();
  }

  render(){
    return(
      <>
      {this.state.error.display?<Alert color="danger">{this.state.error.msg}</Alert>:<Alert className="d-none" color="danger">{this.state.error.msg}</Alert>}
        <Container>
          {this.state.formattedData? <AdminTable toggleInnerAdd={this.getPopUpForGroup} getAddPopUpData={getPopUpForGroupNew}  hasInnerTable={true} isInnerTable={true} updateData={this.getGroups} data={this.state.formattedData}/>:null}
        </Container>
      </>
    );
  }
}
  

export default ViewGroup;
