const tookToday = require('../../../api/calendar/Event').tookToday;
var csrfToken = null;
function formatDependentsWithMeds(dependents){
    var obj = null
    var rows = [];
    //Get Header
    if(typeof(dependents)=='undefined'){
        return null;
    }
    rows.push(getHeaderForDependents());
    for(var i=0;i<dependents.length;++i){
        rows.push(getRowDependentsWithMeds(dependents[i]));
        rows.push(getHeaderForRxs());
        for(var ix=0;ix<dependents[i].rxs.length;++ix){
            if(ix==0){
                //rows.push(getHeaderForRxs());
            }
            var rxs = dependents[i].rxs[ix];
            var medications = [];
            var dependentId = dependents[i]._id.toString();
            for(var ixx=0;ixx<dependents[i].rxs[ix].rxsMedications.length;++ixx){
                medications.push(dependents[i].rxs[ix].rxsMedications[ixx]);
                rows.push(getRowWithMeds(dependents[i].rxs[ix],dependents[i].rxs[ix].rxsMedications[ixx]));
            }
        }
    }
    obj = {
        rows:rows
    }
    return obj;
}
function getHeaderForDependents(){
    var cols = [];
    cols.push("Name:");
    cols.push("");
    cols.push("");
    cols.push("Date of Birth:");
    cols.push("");
    cols.push("");
    cols.push("# Rxs Medication");
    cols.push("");
    var row = {
        isInnerTable:false,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getHeaderForRxs(){
    var cols = [];
    //cols.push("Rxs Number:");
    cols.push("Medication:");
    cols.push("Dosage:");
    cols.push("Reason:");
    cols.push("Instructions:");
    cols.push("When to Take:");
    cols.push("Date Prescribed:");
    cols.push("Doctor's Contact:");
    cols.push("Options:")
    var row = {
        isInnerTable:true,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getColsForDependents(dependent){
    var cols = [];
    cols.push({
        isEditable:false,
        containsDropDown:true,
        type:"text",
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([dependent.name.firstName,dependent.name.lastName],["First Name:","Last Name:"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([""],["Date of Birth:"],null,["dateOfBirth"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([""],["Date of Birth:"],null,["dateOfBirth"])
    });
    cols.push({
        isEditable:false,
        type:"date",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([dependent.dateOfBirth],["Date of Birth:"],null,["dateOfBirth"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([""],["Date of Birth:"],null,["dateOfBirth"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([""],["Date of Birth:"],null,["dateOfBirth"])
    });
    var rxsMedicationsLen =0;
    for(var i=0;i<dependent.rxs.length;++i){
        var curRxs = dependent.rxs[i];
        for(var ix=0;ix<curRxs.rxsMedications.length;++ix){
            rxsMedicationsLen++;
        }
    }
    cols.push({
        isEditable:false,
        type:"test",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([rxsMedicationsLen],["Date of Birth:"],null,["dateOfBirth"])
    });

    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        csrfToken:csrfToken,
        patchReq:null,
        items:getItems([null],[null],
            [{name:"dependent:"+dependent.name.firstName+" "+dependent.name.lastName,
                id:dependent._id.toString(),
                remove:null,
            }]
            ,null)
    });
    return cols;
}
