function getColsForMedication(rxs,medication,dependentId){
    var cols = [];
    var medItems = [];
    var medIds = [];
    var listItems = [];

    cols.push({
        isEditable:false,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([medication.name],["Rxs Number:"],null,["rxsNumber"])
    });

    cols.push({
        isEditable:false,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([medication.dosage],["Rxs Number:"],null,["rxsNumber"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([medication.reason],["Rxs Number:"],null,["rxsNumber"])
    });
    var instructions = "-";
    if(medication.instructions){
        instructions = medication.instructions;
    }
    cols.push({
        isEditable:false,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([instructions],["Rxs Number:"],null,["rxsNumber"])
    });

    var whenToTakeText = "-";
    if(medication.whenToTake.value){
        whenToTakeText = medication.whenToTake.value;
    }   
    cols.push({
        isEditable:false,
        type:"text",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([whenToTakeText],["Rxs Number:"],null,["rxsNumber"])
    });
    cols.push({
        isEditable:false,
        type:"date",
        size:6,
        isList:false,
        containsDropDown:false,
        id:rxs._id,
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([medication.datePrescribed],["Rxs Number:"],null,["rxsNumber"])
    });




    cols.push({
        isEditable:false,
        type:"text",
        isList:false,
        containsDropDown:false,
        id:rxs._id.toString(),
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([rxs.doctorContacts.name.firstName,rxs.doctorContacts.name.lastName,"#"+rxs.doctorContacts.phoneNumber],["Doctor's First Name","Doctor's Last Name"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        isList:false,
        containsDropDown:false,
        id:medication._id.toString(),
        patchReq:null,
        csrfToken:csrfToken,
        items:getItems([null],[null],
            [{name:"prescribtion#"+rxs.rxsNumber,
                id:medication._id.toString(),
                remove:null,
                tookToday:tookToday,
                calendar:true
            }],null)
    });
    return cols;
}
function getRowWithMeds(rxs,medication,dependentId){
    var row = {
        isInnerTable:true,
        isHeader:false,
        cols:getColsForMedication(rxs,medication,dependentId)
    }
    return row;
}
function getRowDependentsWithMeds(dependent){
    var row = {
        isInnerTable:false,
        isHeader:false,
        cols:getColsForDependents(dependent)
    }
    return row;
}
function getItems(valueList,placeHolderList,isObject,path){
    var items = [];
    for(var i=0;i<valueList.length;++i){
        items.push({
            value:valueList[i],
            placeHolder:placeHolderList[i],
            isObject:isObject,
            path:path
        });
    }
    return items;
}

