import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {getPopUpForTookToday} from "../../../medicationUserView";
import {tookToday} from '../../../../../../api/calendar/Event';
import { faTrashAlt, faCalendarPlus,faCalendarAlt} from '@fortawesome/free-solid-svg-icons';

class Edit extends React.Component{
    constructor(props){
        super(props);
    }

    delete = () =>{
        this.props.editObj[0].remove(this.props.editObj[0].id,(res)=>{
            if(res.error){
                alert("error");
            }else{
                this.props.updateData();
            }
        });
    }
    tookToday = () =>{
        var data = getPopUpForTookToday();
        this.props.popUpCustomReq(tookToday,data,this.props.editObj[0].id);
    }

    
    render(){
        let style= {
            paddingLeft:'10px'
        }
        return(
            <>
                {this.props.editObj[0].remove?
                <FontAwesomeIcon className={this.props.styleClass} onClick={this.delete} color={'#e02d1b'} size={'1x'} 
                style={{cursor:'pointer',verticalAlign: 'middle',fontSize:'13px'}}icon={faTrashAlt}/>
                :null}
                {
                this.props.editObj[0].tookToday?
                <FontAwesomeIcon className={this.props.styleClass} onClick={this.tookToday} color={'#4d72df'} size={'1x'} 
                style={{cursor:'pointer',verticalAlign: 'middle',marginRight:'10px',fontSize:'13px'}}icon={faCalendarPlus}/>
                :
                null}
                {
                this.props.editObj[0].calendar?
                <FontAwesomeIcon className={this.props.styleClass} onClick={()=>{alert("Feature coming soon.")}}
                 color={'#f4b619'} size={'1x'} style={{cursor:'pointer',verticalAlign: 'middle',fontSize:'13px'}}
                 icon={faCalendarAlt}/>
                :null}
                {/* <FontAwesomeIcon className={this.props.styleClass} color={'#5E72E4'} size={'1x'} style={{cursor:'pointer',marginLeft:'10px',verticalAlign: 'middle'}}icon={faDownload}/> */}
            </>
        )
    }
}
export default Edit;