const groupPatch = require('../../../api/groups/Groups').patch;
const groupCreate = require('../../../api/groups/Groups').create;
const groupDelete = require('../../../api/groups/Groups').deleteGroup;
const removeGuardianFromGroup = null;
const removeDependentFromGroup = null;
const variable = require('../../../config/variable');

var jwt = localStorage.getItem("JWT");
function getAllGuardians(callback){
    fetch(variable.api.baseURL+"/guardians/"+jwt,{
        method:'GET',
        headers: {
            'Content-Type': 'application/json',
        },
    }).then(response => response.json())
    .then(jsondata => callback(jsondata));
}
function getAllDependents(callback){
    fetch(variable.api.baseURL+"/dependents/"+jwt,{
        method:'GET',
        headers: {
            'Content-Type': 'application/json',
        },
    }).then(response => response.json())
    .then(jsondata => callback(jsondata));
}
function getGroupById(id,callback){
    fetch(variable.api.baseURL+"/groups/"+id+"/"+jwt,{
        method:'GET',
        headers: {
            'Content-Type': 'application/json',
        },
    }).then(response => response.json())
    .then(jsondata => callback(jsondata));
}


function formatGroups(groups){
    var obj = null
    var rows = [];
    //Get Header
    rows.push(getHeaderForGroups());
    for(var i=0;i<groups.length;++i){
        rows.push(getRowGroups(groups[i]));
        rows.push(getHeaderForDependents());
        for(var ix=0;ix<groups[i].dependents.length;++ix){
            rows.push(getRowDependents(groups[i].dependents[ix]));
        }
        rows.push(getHeaderForGuadians());
        for(var ix=0;ix<groups[i].guardians.length;++ix){
            rows.push(getRowGuardians(groups[i].guardians[ix]));
        }
    }
    obj = {
        rows:rows
    }
    return obj;
}
function getHeaderForGroups(){
    var cols = [];
    cols.push("Group Name:");
    cols.push("# of Dependent(s):");
    cols.push("# of Guardian(s):");
    cols.push("Options:")
    var row = {
        isInnerTable:false,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getHeaderForGuadians(){
    var cols = [];
    cols.push("Guardian's Name:");
    cols.push("Guardian's Phone Number:");
    cols.push("");
    cols.push("Options:")
    var row = {
        isInnerTable:true,
        isHeader:true,
        cols:cols
    }
    return row;
}
function getHeaderForDependents(){
    var cols = [];
    cols.push("Dependent's Name:");
    cols.push("Dependent's DOB:");
    cols.push("");
    cols.push("Options:")
    var row = {
        isInnerTable:true,
        isHeader:true,
        cols:cols
    }
    return row;
}

function getColsForGroups(group){
    var cols = [];
    cols.push({
        isEditable:true,
        containsDropDown:true,
        type:"text",
        size:6,
        id:group._id.toString(),
        patchReq:groupPatch,
        items:getItems([group.name],["Name:"],null,["name"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        // type:"selectList",
        id:group._id.toString(),
        patchReq:groupPatch,
        items:getItems([group.dependents.length],["Dependents:"],null,["dependent"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        // type:"selectList",
        options:["false","true"],
        id:group._id.toString(),
        patchReq:groupPatch,
        items:getItems([group.guardians.length],["Guardians:"],null,["guardian"])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:group._id.toString(),
        patchReq:groupPatch,
        items:getItems([null],[null],
            [{name:"group:"+group.groupname,
                id:group._id.toString(),
                remove:groupDelete
            }]
            ,null)
    });
    return cols;
}

function getColsForGuardians(guardian){
    var cols = [];
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        id:guardian._id.toString(),
        patchReq:null,
        items:getItems([guardian.name.firstName,guardian.name.lastName],["firstName","lastName"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        id:guardian._id.toString(),
        patchReq:null,
        items:getItems([guardian.phoneNumber],["phoneNumber"],null,["phoneNumber"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        id:guardian._id.toString(),
        patchReq:null,
        items:getItems([""],[""],null,[""])
    });
    cols.push({
        isEditable:false,
        type:"text",
        id:guardian._id.toString(),
        patchReq:null,
        items:getItems([null],[null],
            [{name:"guardian:"+guardian.name,
                id:guardian._id.toString(),
                remove:removeGuardianFromGroup
            }]
            ,null)
    });
    return cols;
}

function getColsForDependents(dependent){
    var cols = [];
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        id:dependent._id.toString(),
        patchReq:null,
        items:getItems([dependent.name.firstName,dependent.name.lastName],["firstName","lastName"],null,["firstName","lastName"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"date",
        id:dependent._id.toString(),
        patchReq:null,
        items:getItems([dependent.dateOfBirth],["dateOfBirth"],null,["dateOfBirth"])
    });
    cols.push({
        isEditable:false,
        containsDropDown:false,
        type:"text",
        id:dependent._id.toString(),
        patchReq:null,
        items:getItems([""],[""],null,[""])
    });
    cols.push({
        isEditable:false,
        type:"text",
        containsDropDown:false,
        id:dependent._id.toString(),
        patchReq:null,
        items:getItems([null],[null],
            [{name:"dependent:"+dependent.name,
                id:dependent._id.toString(),
                remove:removeDependentFromGroup
            }]
            ,null)
    });
    return cols;
}

function getRowGroups(group){
    var row = {
        isInnerTable:false,
        isHeader:false,
        cols:getColsForGroups(group)
    }
    return row;
}
function getRowGuardians(guardian){
    var row = {
        isInnerTable:true,
        isHeader:false,
        cols:getColsForGuardians(guardian)
    }
    return row;
}
function getRowDependents(dependent){
    var row = {
        isInnerTable:true,
        isHeader:false,
        cols:getColsForDependents(dependent)
    }
    return row;
}
function getItems(valueList,placeHolderList,isObject,path){
    var items = [];
    for(var i=0;i<valueList.length;++i){
        items.push({
            value:valueList[i],
            placeHolder:placeHolderList[i],
            isObject:isObject,
            path:path
        });
    }
    return items;
}
function getPopUpForGroup(selfId){
    return new Promise(function(resolve, reject) {
        var data = [];
        var dependentOptions = [];

        getAllDependents(function(res){
            if(res.error){
                reject(res.error);
            }else{
                dependentOptions = res;
                getGroupById(selfId,function(res){
                    if(res.error){
                        reject(res.error);
                    }else{
                        var options = [];
                        for(var i=0;i<dependentOptions.length;++i){
                            var value = false;
                            for(var ix=0;ix<res.dependents.length;++ix){
                                if(dependentOptions[i]._id.toString() == res.dependents[ix].toString()){
                                    value = true;
                                }
                            }
                            options.push({
                                isSelected:value,
                                label:dependentOptions[i].name.firstName+" "+dependentOptions[i].name.lastName,
                                value:dependentOptions[i]._id.toString()
                            });
                        }
                    }
                    data.push({
                        type:"multiSelect",
                        options:options,
                        validator:null,
                        label:"Select Dependent(s):",
                        required:false,
                        path:"dependents",
                        reqCreate:groupPatch,
                        reqPatch:groupPatch,
                        parentId:selfId
                    });
                    var guardianOptions;
                    getAllGuardians(function(res){
                        if(res.error){
                            reject(res.error);
                        }else{
                            guardianOptions = res;
                            getGroupById(selfId,function(res){
                                if(res.error){
                                    reject(res.error);
                                }else{
                                    var options = [];
                                    for(var i=0;i<guardianOptions.length;++i){
                                        var value = false;
                                        for(var ix=0;ix<res.guardians.length;++ix){
                                            if(guardianOptions[i]._id.toString() == res.guardians[ix].toString()){
                                                value = true;
                                            }
                                        }
                                        options.push({
                                            isSelected:value,
                                            label:guardianOptions[i].name.firstName+" "+guardianOptions[i].name.lastName,
                                            value:guardianOptions[i]._id.toString()
                                        });
                                    }

                                    data.push({
                                        type:"multiSelect",
                                        options:options,
                                        validator:null,
                                        label:"Select Guardian(s):",
                                        required:false,
                                        path:"guardians",
                                        reqCreate:groupPatch,
                                        selfId:selfId,
                                        optionsObje:null,
                                        parentId:null
                                    });
                                    resolve(data);
                                }
                            });
                        }
                    });
                });
            }
        });
    });
}
function getPopUpForGroupNew(){
    var data = [];

    data.push({
        value:"",
        type:"text",
        validator:null,
        label:"Name of Group:",
        placeHolder:"Enter name of group.",
        required:true,
        path:"name",
        reqCreate:groupCreate,
        selfId:null,
        parentId:null
    });

    return data;
}
export{formatGroups,getPopUpForGroup,getPopUpForGroupNew}