import React from 'react';
import {Table,Card,Container, Row, Col, Button} from 'reactstrap';

import Header from './Header/Header';
import Body from './Body/Body';
import PagePopUp from '../PagePopUp/PagePopUp';
import {getPopUpForDependent,getPopUpForRxs,getPopUpForListRxsMedications} from './medicationFormatter';

class AdminTable extends React.Component{
    state = {
        headerData:null,
        rowsData:null,
        popUpData:[],
        popUpKey:null,
        isAddForPopUp:false,
        showPopUp:false,
        createSelf:false,
        isAddData:null,
        rxsMedId:null,
        popUpCustomReq:null
    }
    constructor(props){
        super(props);
    }
    loadData(){
        let newState = this.state;
        newState.headerData = this.getHeader();
        newState.rowsData = this.getRows();
        this.setState(newState);
    }
    getHeader(){
        var header = this.state.headerData;
        for(var i=0;i<this.props.data.rows.length;++i){
            var row = this.props.data.rows[i];
            if(row.isHeader && !row.isInnerTable){
                header = row;
                return header;
            }
        }
    }
    getRows(){
        var rows = [];
        for(var i=0;i<this.props.data.rows.length;++i){
            var row = this.props.data.rows[i];
            if(!row.isHeader || row.isInnerTable){
               rows.push(row);
            }
        }
        return rows;
    }
    componentDidMount(){
        this.loadData();
    }
    togglePopUp = (data,isAdd,key,createSelf) =>{
        let newState = this.state;
        newState.popUpData = data;
        newState.isAddForPopUp = isAdd;
        if(!isAdd){
            newState.popUpKey = key;
        }else{
            newState.popUpKey = null;
        }
        this.state.createSelf = createSelf;
        newState.showPopUp = !newState.showPopUp;
        this.setState(newState);
    }
    togglePopUpCustomReq = (req,data,rxsMedId) =>{
        var newData = [];
        newData.push(data);
        let newState = this.state;
        newState.popUpData = newData;
        newState.isAddForPopUp = true;
        newState.rxsMedId = rxsMedId;
        newState.showPopUp = !newState.showPopUp;
        newState.popUpCustomReq = req;
        this.setState(newState);
    }
    togglePopUpAdd = () =>{
        this.togglePopUp([this.props.getAddPopUpData()],true,null,true);
    }
    toggleInnerPopUpAdd = async(id) =>{
        let res = await this.props.toggleInnerAdd(id);
        this.togglePopUp([res],true,null,false);
    }
    toggleInnerPopUpAddForList = (id) =>{
        this.togglePopUp([getPopUpForListRxsMedications(id)],true,null,false);
    }
    render(){
        return(
            <>
                {this.state.showPopUp && this.state.isAddForPopUp?
                < PagePopUp customReq={this.state.popUpCustomReq} rxsMedId ={this.state.rxsMedId} 
                createSelf={this.state.createSelf} listPopUp={this.togglePopUp} isAdd = {this.state.isAddForPopUp
                } data={this.state.popUpData[0]} updateData={this.props.updateData}/>
                :this.state.showPopUp?
                <>
                 <PagePopUp customReq={this.state.popUpCustomReq}
                  rxsMedId ={this.state.rxsMedId} createSelf={this.state.createSelf}
                   listPopUp={this.togglePopUp} isAdd = {this.state.isAddForPopUp} 
                   data={this.state.popUpData[this.state.popUpKey]} updateData={this.props.updateData}/>
                 </>
                :null}
                {this.props.isUser != true?
                <Row>
                    <Col lg="2" style={{marginLeft:"0px",paddingLeft:"0px"}}>
                        <Button onClick={this.togglePopUpAdd} style={{width:"100%",marginBottom:"30px",marginLeft:"0px"}}color="primary">Add</Button>
                    </Col>
                </Row>
                :null}
                <Row>
                    <Table style={{borderCollapse:"separate"}}>
                        {this.state.headerData? <Header style={{borderCollapse:"collapse"}}headerItems={this.state.headerData.cols}/>:null}
                        <tbody style={{border:"none"}}>
                            {this.state.headerData? <Body isUser={this.props.isUser} hasInnerTable={this.props.hasInnerTable} 
                            popUpCustomReq={this.togglePopUpCustomReq} innerPopUp={this.toggleInnerPopUpAdd} addHandler={this.togglePopUp} 
                            listPopUp={this.togglePopUp} updateData={this.props.updateData} rows={this.state.rowsData}/> :null}
                        </tbody>
                    </Table>
                </Row>
            </>
        );
    }
}
export default AdminTable;